<template>
  <page-layout :avatar="avatar" id="workplace">
    <div slot="headerContent">
      <div class="title">{{ timeFix }}，{{ user.name }}<span class="welcome-text">，{{ welcome() }}</span></div>
      <div>座席6003 | 搬砖大队 - 队长</div>
    </div>
    <div slot="extra">
      <a-row class="more-info">
        <a-col :span="8">
          <head-info title="首拨名单数" content="56" :center="false" :bordered="false"/>
        </a-col>
        <a-col :span="8">
          <head-info title="今日预约" content="3" :center="false" :bordered="false"/>
        </a-col>
        <a-col :span="8">
          <head-info title="团队排名" content="8/24" :center="false" />
        </a-col>
      </a-row>
    </div>

    <div>
      <a-row :gutter="24">
        <a-col :xl="16" :lg="24" :md="24" :sm="24" :xs="24">
          <a-card 
            class="project-list" 
            :loading="starLoading" 
            style="margin-bottom: 24px;"     
            :bordered="false" 
            title="今日之星"
            :body-style="{ padding: starLoading?'24px':0 }">
            <a slot="extra">全部</a>
            <div>
              <a-card-grid class="project-card-grid" :key="i" v-for="(item, i) in projects">
                <a-card :bordered="false" :body-style="{ padding: 0 }">
                  <a-card-meta>
                    <div slot="title" class="card-title">
                      <a-avatar size="small" :src="item.avatar"/>
                      <a>{{ item.nickname||'昵称'}}</a>
                    </div>
                    <div slot="description" class="card-description">
                      {{ item.description||'没有填写描述信息...' }}
                    </div>
                  </a-card-meta>
                  <div class="project-item">
                    <a href="/#/">{{item.team}}</a>
                    <span class="datetime">
                      <a-tooltip :title="item.liked ? '取消赞' : '点赞'">
                        <a-icon
                          type="like"
                          :theme="item.liked ? 'filled' : 'outlined'"
                          @click="like(item)"
                        />
                      </a-tooltip>
                      <!-- <span style="padding-left: '8px';cursor: 'auto'">
                        {{likes}}
                      </span> -->
                    </span>
                  </div>
                </a-card>
              </a-card-grid>
            </div>
          </a-card>

          <a-card :loading="activitiesLoading" title="动态" :bordered="false">
            <a-list>
              <a-list-item :key="index" v-for="(item, index) in activities">
                <a-list-item-meta>
                  <a-avatar slot="avatar" :src="item.user.avatar" />
                  <div slot="title">
                    <span>{{ item.user.nickname }}</span>&nbsp;
                    在&nbsp;<a href="#">{{ item.project.name }}</a>&nbsp;
                    <span>{{ item.project.action }}</span>&nbsp;
                    <a href="#">{{ item.project.event }}</a>
                  </div>
                  <div slot="description">{{ item.time }}</div>
                </a-list-item-meta>
              </a-list-item>
            </a-list>
          </a-card>
        </a-col>
        <a-col 
          style="padding: 0 12px" 
          :xl="8" 
          :lg="24" 
          :md="24" 
          :sm="24" 
          :xs="24">
          <a-card :loading="teamsLoading" class="convenient-navigation" title="便捷导航" style="margin-bottom: 24px" :bordered="false">
            <div class="item-group" style="font-size: 0;">
              <a>操作一</a>
              <a>操作二</a>
              <a>操作三</a>
              <a>操作四</a>
              <a>操作五</a>
              <a>操作六</a>
              <a-button size="small" type="primary" ghost icon="plus" style="margin-bottom:0">添加</a-button>
            </div>
          </a-card>
          <!-- <a-card title="XX 指数" style="margin-bottom: 24px" :loading="radarLoading" :bordered="false" :body-style="{ padding: 0 }">
            <div style="min-height: 400px;">
              <radar :data="radarData" />
            </div>
          </a-card> -->
          <a-card :loading="teamsLoading" title="团队" :bordered="false">
            <div class="members">
              <a-row>
                <a-col :span="12" v-for="(item, index) in teams" :key="index">
                  <a>
                    <a-avatar size="small" :src="item.avatar" />
                    <span class="member">{{ item.name }}</span>
                  </a>
                </a-col>
              </a-row>
            </div>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </page-layout>
</template>

<script>
  import { timeFix } from '@/utils/util'
  import {mapGetters} from 'vuex'

  import PageLayout from '@/components/Page/PageLayout'
  import HeadInfo from '@/components/Tools/HeadInfo'
  import ATooltip from 'ant-design-vue/lib/tooltip'
  import AIcon from 'ant-design-vue/lib/icon'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ACard from 'ant-design-vue/lib/card'
  import AAvatar from 'ant-design-vue/lib/avatar'
  import AList from 'ant-design-vue/lib/list'
  import AButton from 'ant-design-vue/lib/button'

  // import Radar from '@/components/chart/Radar'

  // import { getRoleList, getServiceList } from '@/api/manage'

  import { getTodayStar, getActivity, getTeams } from '@/api/workplace'
import { debug } from 'util';

  const ACardGrid = ACard.Grid
  const ACardMeta = ACard.Meta
  const AListItem = AList.Item
  const AListItemMeta = AListItem.Meta

  const DataSet = require('@antv/data-set')


  export default {
    name: 'Workplace',
    components: {
      PageLayout,
      HeadInfo,
      ATooltip,
      AIcon,
      ARow,
      ACol,
      ACard,
      ACardGrid,
      ACardMeta,
      AAvatar,
      AList,
      AListItem,
      AListItemMeta,
      AButton
      // Radar
    },
    data() {
      return {
        timeFix: timeFix(),
        avatar: '',
        user: {},

        projects: [],
        loading: true,
        starLoading:true,
        teamsLoading:true,
        activitiesLoading:true,
        radarLoading: true,
        activities: [],
        teams: [],

        // data
        axis1Opts: {
          dataKey: 'item',
          line: null,
          tickLine: null,
          grid: {
            lineStyle: {
              lineDash: null
            },
            hideFirstLine: false
          }
        },
        axis2Opts: {
          dataKey: 'score',
          line: null,
          tickLine: null,
          grid: {
            type: 'polygon',
            lineStyle: {
              lineDash: null
            }
          }
        },
        scale: [{
          dataKey: 'score',
          min: 0,
          max: 80
        }],
        axisData: [
          { item: '引用', a: 70, b: 30, c: 40 },
          { item: '口碑', a: 60, b: 70, c: 40 },
          { item: '产量', a: 50, b: 60, c: 40 },
          { item: '贡献', a: 40, b: 50, c: 40 },
          { item: '热度', a: 60, b: 70, c: 40 },
          { item: '引用', a: 70, b: 50, c: 40 }
        ],
        radarData: []
      }
    },
    computed: {
      userInfo() {
        return this.$store.getters.userInfo
      }
    },
    created() {
      this.user = this.userInfo
      this.avatar = this.userInfo.avatar

      // getRoleList().then(res => {
      //   console.log('workplace -> call getRoleList()', res)
      // })

      // getServiceList().then(res => {
      //   console.log('workplace -> call getServiceList()', res)
      // })
    },
    mounted() {
      this.getTodayStarFromServer()
      this.getActivityFromServer()
      this.getTeamsFromServer()
      // this.initRadar()
    },
    methods: {
      ...mapGetters(['nickname', 'welcome']),
      like(user) {
        user.liked = !user.liked
      },
      getTodayStarFromServer() {
        let _this = this
        getTodayStar()
          .then(res => {
            _this.projects = res.data
            _this.starLoading = false
          }).catch((err) => {
            _this.starLoading = false
          })
      },
      getActivityFromServer() {
        let _this = this
        getActivity()
          .then(res => {
            _this.activities = res.data
            _this.activitiesLoading = false
          }).catch((err) => {
            _this.activitiesLoading = false
          })
      },
      getTeamsFromServer() {
        let _this = this
        getTeams()
          .then(res => {
            _this.teams = res.data
            _this.teamsLoading = false
          }).catch((err) => {
            _this.teamsLoading = false
          })
      },
      initRadar() {
        this.radarLoading = true

        this.$http.get('/workplace/radar')
          .then(res => {

            const dv = new DataSet.View().source(res.result)
            dv.transform({
              type: 'fold',
              fields: ['个人', '团队', '部门'],
              key: 'user',
              value: 'score'
            })

            this.radarData = dv.rows
            this.radarLoading = false
          })
      }
    }
  }
</script>

<style lang="scss" scoped>
  #workplace div.content div.page-header-index-wide > div{
    padding: 0;
    margin: 0;
    background-color: transparent
  }
  .project-list {

    .card-title {
      font-size: 0;

      a {
        color: rgba(0, 0, 0, 0.85);
        margin-left: 12px;
        line-height: 24px;
        height: 24px;
        display: inline-block;
        vertical-align: top;
        font-size: 14px;

        &:hover {
          color: #1890ff;
        }
      }
    }
    .card-description {
      color: rgba(0, 0, 0, 0.45);
      height: 44px;
      line-height: 22px;
      overflow: hidden;
    }
    .project-item {
      display: flex;
      margin-top: 8px;
      overflow: hidden;
      font-size: 12px;
      height: 20px;
      line-height: 20px;
      a {
        color: rgba(0, 0, 0, 0.45);
        display: inline-block;
        flex: 1 1 0;

        &:hover {
          color: #1890ff;
        }
      }
      .datetime {
        color: rgba(0, 0, 0, 0.25);
        flex: 0 0 auto;
        float: right;
        cursor: pointer
      }
    }
    .ant-card-meta-description {
      color: rgba(0, 0, 0, 0.45);
      height: 44px;
      line-height: 22px;
      overflow: hidden;
    }
    
  }

  .item-group {
    a {
      color: rgba(0, 0, 0, 0.65);
      display: inline-block;
      font-size: 14px;
      margin-bottom: 13px;
      width: 25%;
    }
  }

  .members {
    a {
      display: block;
      margin: 12px 0;
      line-height: 24px;
      height: 24px;
      .member {
        font-size: 14px;
        color: rgba(0, 0, 0, .65);
        line-height: 24px;
        max-width: 100px;
        vertical-align: top;
        margin-left: 12px;
        transition: all 0.3s;
        display: inline-block;
      }
      &:hover {
        span {
          color: #1890ff;
        }
      }
    }
  }

  .mobile {

    .project-list {

      .project-card-grid {
        width: 100%;
      }
    }

    .more-info {
      border: 0;
      padding-top: 16px;
      margin: 16px 0 16px;
    }

    .headerContent .title .welcome-text {
      display: none;
    }
  }

</style>

<style>
div.project-list>.ant-card-body{
  transition:padding 0s
}
</style>


